import React, { useState, useEffect, useRef } from "react";

// reactstrap components
import { Button, Container, Row, Col } from "reactstrap";

// core components
import { Consumer } from '../../Config/Context';
import Animate from 'subcomponents/Animate';
import Icons from 'subcomponents/Icons';
import SectionNucleoIcons from 'views/index-sections/SectionNucleoIcons';
const svgs = [
  "mercedes-prefooter",
  "kompressor-prefooter",
  "amg-prefooter",
  "clasec-prefooter"
]


function PreFooter() {
  const [ clase, setClase] =  useState(svgs[0]);
  const [ count, setCount] =  useState(0);
  
  const tope = svgs.length;

   useInterval(() => {
      if(tope===count){
          setCount(0)
      }else{
      setCount(count + 1)
      setClase(svgs[count])
      }
      // console.log(count)        
   },5000)

  return (
    <Consumer>
      { ( {idioma, Config} ) => {
        let preFooter = Config.footer[idioma].preFooter
        let { descripcion } = preFooter
        return(
              <>
                <div className="section section-dark section-nucleo-icons">
                  <Container>
                    <Row>
                      <Col lg="6" md="12">
                        {/*<h2 className="title"></h2>*/}
                        {/*<div className="gova2 blanco grande gova-prefooter"></div>*/}
                        <br />
                        <p className="description">
                            {descripcion}
                        </p>
                        <br />
                        
                        <Row>
                          <Col lg="12" md="12">
                            <Animate config={{clase:"phone blanco phone-size grande",animacion:"swing", tiempo : "7s",infinite:true}}></Animate>
                            <div className="numero_"></div>
                          </Col>                          
                        </Row>

                      </Col>
                      <Col lg="6" md="12">
                          <div className="icons-container">
                            <i className="nc-icon nc-time-alarm" />
                            <i className="nc-icon nc-atom" />
                            <i className="nc-icon nc-camera-compact" />
                            <i className="nc-icon nc-watch-time" />
                            <i className="nc-icon nc-key-25" />
                            <i className="nc-icon nc-diamond" />
                            <i className="nc-icon nc-user-run" />
                            <i className="nc-icon nc-layout-11" />
                            <i className="nc-icon nc-badge" />
                            <i className="nc-icon nc-bulb-63" />
                            <i className="nc-icon nc-favourite-28" />
                            <i className="nc-icon nc-planet" />
                            <i className="nc-icon nc-tie-bow" />
                            <i className="nc-icon nc-zoom-split" />
                            <i className="nc-icon nc-cloud-download-93" />
                          </div>
                        {/*<div className={ clase + " blanco icons-size-prefooter"}></div>*/}
                        {/*
                        <Animate config={{
                            animacion:"fadeOut",
                            clase: clase + " blanco icons-size-prefooter",
                            tiempo:"10s",
                            infinite:true
                        }}></Animate>
                        
                        */}
                      </Col>
                    </Row>
                  </Container>
                </div>{" "}
              </>

        )
      }}
    </Consumer>
  );
}

function useInterval(callback, delay) {
    const savedCallback = useRef();

    // Remember the latest function.
    useEffect(() => {
      savedCallback.current = callback;
    }, [callback]);

    // Set up the interval.
    useEffect(() => {
      function tick() {
        savedCallback.current();
      }
      if (delay !== null) {
        let id = setInterval(tick, delay);
        return () => clearInterval(id);
      }
    }, [delay]);
}

export default PreFooter;
