import React from "react";

import { Consumer } from 'Config/Context';
// reactstrap components
import { Button, Container, Row, Col, UncontrolledTooltip } from "reactstrap";

// core components
function SectionShared() {
  return (
    <Consumer>
      { ({ idioma, Config }) => {
          let shared = Config.body[idioma].shared;
          let {google,facebook,twitter, titulo} =  shared
          return(
                <>
                  <div className="section">
                    <Container className="text-center">
                      <Row className="justify-content-md-center sharing-area text-center">
                        <Col className="text-center" lg="8" md="12">
                          <h3>{titulo}</h3>
                        </Col>
                        <Col className="text-center" lg="8" md="12">
                          <Button
                            className="twitter-sharrre btn-round"
                            color="twitter-bg"
                            href={google}
                            id="tooltip3373767"
                            onClick={e => e.preventDefault()}
                          >
                            <i className="fa fa-twitter" /> Twitter
                          </Button>
                          <UncontrolledTooltip delay={0} target="tooltip3373767">
                            Tweet!
                          </UncontrolledTooltip>
                          <Button
                            className="linkedin-sharrre btn-round  ml-2"
                            color="google-bg"
                            href={twitter}
                            id="tooltip840791273"
                            onClick={e => e.preventDefault()}
                          >
                            <i className="fa fa-google-plus" /> Google
                          </Button>
                          <UncontrolledTooltip delay={0} target="tooltip840791273">
                            Share!
                          </UncontrolledTooltip>
                          <Button
                            className="facebook-sharrre btn-round ml-2"
                            color="facebook-bg"
                            href={facebook}
                            id="tooltip68961360"
                            onClick={e => e.preventDefault()}
                          >
                            <i className="fa fa-facebook-square" /> Facebook
                          </Button>
                          <UncontrolledTooltip delay={0} target="tooltip68961360">
                            Share!
                          </UncontrolledTooltip>
                        </Col>
                      </Row>
                    </Container>
                  </div>
                </>
          )
      } }
    </Consumer>
  );
}

export default SectionShared;
